import mapboxgl from "mapbox-gl";
import 'mapbox-gl/dist/mapbox-gl.css';
import MapboxGeocoder from '@mapbox/mapbox-gl-geocoder';
import '@mapbox/mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css';
import { COMPANY_JOB_LIST, DEALS_DETAIL, ADD_FAVORITE, IS_FAVORITE, CANCEL_FAVORITE, ADD_TO_CHAT, USER_INFO_VISITOR_V2, SWITCH_IDENTITY_V2 } from "@/api/api";
import { useStore } from "vuex";
import { encode } from "js-base64";
export default {
  name: "detail",

  setup() {
    const store = useStore();

    const setNowChatUserInfo = data => store.commit('nowChatUserInfo', data);

    const setShowChatStatus = () => store.commit('showChatStatus', true);

    const locationUrl = window.location.href;
    return {
      setNowChatUserInfo,
      setShowChatStatus,
      locationUrl
    };
  },

  data() {
    return {
      accessToken: process.env.VUE_APP_MAP_BOX_ACCESS_TOKEN,
      mapStyle: process.env.VUE_APP_MAP_BOX_STYLE,
      detailData: {},
      otherJobsData: [],
      isFavoriteValue: 0,
      showSocialShareExpandStatus: false
    };
  },

  components: {},

  mounted() {
    let dealId = this.$route.query.id;
    this.getDealDetail(dealId);
    let token = localStorage.getItem('token');

    if (token && token != '') {
      this.isFavorite(2, dealId);
    }
  },

  methods: {
    viewCompanyProfile(id) {
      this.$router.push({
        path: '/info/company',
        query: {
          id: id,
          identity: 3
        }
      });
    },

    initMap(lng, lat) {
      mapboxgl.accessToken = this.accessToken;
      const map = new mapboxgl.Map({
        container: "mapContainer",
        center: [lng, lat],
        style: this.mapStyle,
        zoom: 12
      });
      const nav = new mapboxgl.NavigationControl();
      map.addControl(nav, "top-right");
      const geolocate = new mapboxgl.GeolocateControl({
        positionOptions: {
          enableHighAccuracy: true
        },
        trackUserLocation: true
      });
      map.addControl(geolocate, "top-right");
      const geocoder = new MapboxGeocoder({
        "accessToken": this.accessToken,
        "mapboxgl": mapboxgl
      });
      map.addControl(geocoder, 'top-left');
      const marker = new mapboxgl.Marker();
      marker.setLngLat([lng, lat]).addTo(map);
    },

    getDealDetail(id) {
      let params = {
        deal_id: id,
        token: localStorage.getItem('token')
      };
      DEALS_DETAIL(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.detailData = res.message;
          this.initMap(res.message.lng, res.message.lat);
          let userId = res.message.user_id;
          this.getCompanyJobList(userId);
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    getCompanyJobList(userId) {
      let params = {
        user_id: userId,
        is_open: 1,
        status: 1,
        page: 1,
        limit: 5
      };
      COMPANY_JOB_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.otherJobsData = res.message.data;
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    addFavorite(id, type, title, url) {
      let params = {
        token: localStorage.getItem('token'),
        type: type,
        type_id: id,
        type_title: title,
        type_url: url
      };
      ADD_FAVORITE(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.$message.success('Success');
          this.isFavoriteValue = 1;
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    cancelFavorite(type, typeId) {
      let params = {
        token: localStorage.getItem('token'),
        type: type,
        type_id: typeId
      };
      CANCEL_FAVORITE(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.isFavoriteValue = 0;
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    isFavorite(type, typeId) {
      let params = {
        token: localStorage.getItem('token'),
        type: type,
        type_id: typeId
      };
      IS_FAVORITE(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.isFavoriteValue = res.data;
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    chat(userId) {
      console.log(userId);
      let token = localStorage.getItem('token');

      if (!token || token === '') {
        return this.$router.push('/login');
      }

      let vendorInfo = this.detailData.userInfo;
      let type = this.GoEasy.IM_SCENE.PRIVATE;
      let name = vendorInfo.first_name + ' ' + vendorInfo.last_name;
      let avatar = vendorInfo.profile_photo ? vendorInfo.profile_photo : 'https://oss.esl-passport.cn/educator.png';
      let nowUserInfo = {
        uuid: userId,
        name: name,
        avatar: avatar,
        identity: 3
      };
      let textMessage = this.goEasy.im.createTextMessage({
        text: 'Hello',
        to: {
          id: userId,
          type: type,
          data: {
            name: name,
            avatar: avatar,
            identity: 3
          }
        }
      });
      let localHistory;

      if (type === this.GoEasy.IM_SCENE.PRIVATE) {
        localHistory = this.service.getPrivateMessages(userId);
      } else {
        localHistory = this.service.getGroupMessages(userId);
      } // console.log(localHistory)


      localHistory.push(textMessage);
      this.goEasy.im.sendMessage({
        message: textMessage,
        onSuccess: function (message) {
          console.log("发送成功.", message);
        },
        onFailed: function (error) {
          console.log("发送失败:", error);
        }
      });
      this.setShowChatStatus();
      this.setNowChatUserInfo(nowUserInfo);
    },

    addUserToChat(userId, toIdentity, identity) {
      let params = {
        identity: identity,
        to_identity: toIdentity,
        to_user_id: userId
      };
      ADD_TO_CHAT(params).then(res => {
        if (res.code === 200) {
          console.log('add Chat successfuly');
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    offerDeal() {
      let token = localStorage.getItem('token');
      let self = this;

      if (!token || token == '') {
        return this.$msgbox({
          title: 'Offer a Deal',
          message: 'Before offer a deal, you need to log in',
          type: 'warning',
          confirmButtonText: 'Log in',

          callback(action) {
            console.log(action);

            if (action === 'confirm') {
              let redirectParamsObj = {
                path: '/deals/detail',
                query: {
                  id: self.$route.query.id
                }
              };
              let redirectParamsStr = encode(JSON.stringify(redirectParamsObj));
              self.$router.push({
                path: '/login',
                query: {
                  redirect_params: redirectParamsStr
                }
              });
            }
          }

        });
      }

      let identity = localStorage.getItem('identity');

      if (identity != 3) {
        this.selectRole(3);
      }

      self.$router.push({
        path: '/deals/offer',
        query: {}
      });
    },

    selectRole(e) {
      this.$loading({
        text: 'Loading...'
      });
      let params = {
        user_id: localStorage.getItem('uid'),
        identity: e
      };
      USER_INFO_VISITOR_V2(params).then(res => {
        let userContact = res.message.user_contact;
        let educatorContact = {};
        let companyInfo = {};
        let isEducator = userContact.is_educator;
        let isRecruiting = userContact.is_recruiting;
        let isSchool = userContact.is_school;
        let isOther = userContact.is_other;
        let isVendor = userContact.is_vendor;
        let identity = e;

        if (identity == 1) {
          if (isEducator > 10) {
            educatorContact = res.message.user_contact.educator_contact;
            this.changeIdentity(educatorContact.id, 1, 2);
            this.$loading().close();
          } else {
            this.$loading().close(); // this.$message.warning('Oops!.. Your profile is incomplete. ')

            this.$router.push({
              path: '/profile/contact/user',
              query: {
                i: 1
              }
            });
          }
        }

        if (identity == 2) {
          if (isRecruiting > 10) {
            companyInfo = res.message.user_contact.company;
            this.changeIdentity(companyInfo.id, 2, 2); // this.$router.push({path: '/overview', query: {identity: identity}})

            this.$loading().close();
          } else {
            this.$loading().close(); // this.$message.warning('Oops!.. Your profile is incomplete. ')

            this.$router.push({
              path: '/profile/contact/user',
              query: {
                i: 2
              }
            });
            this.dialogBusinessAccountVisible = false;
          }
        }

        if (identity == 3) {
          if (isSchool > 10) {
            companyInfo = res.message.user_contact.company;
            this.changeIdentity(companyInfo.id, 3, 2); // this.$router.push({path: '/overview', query: {identity: identity}})

            this.$loading().close();
          } else {
            this.$loading().close(); // this.$message.warning('Oops!.. Your profile is incomplete. ')

            this.$router.push({
              path: '/profile/contact/user',
              query: {
                i: 3
              }
            });
            this.dialogBusinessAccountVisible = false;
          }
        }

        if (identity == 4) {
          if (isOther > 10) {
            companyInfo = res.message.user_contact.company;
            this.changeIdentity(companyInfo.id, 4, 2);
            this.$loading().close();
          } else {
            this.$loading().close(); // this.$message.warning('Oops!.. Your profile is incomplete. ')

            this.$router.push({
              path: '/profile/contact/user',
              query: {
                i: 4
              }
            });
            this.dialogBusinessAccountVisible = false;
          }
        }

        if (identity == 5) {
          if (isVendor > 10) {
            companyInfo = res.message.user_contact.company;
            this.changeIdentity(companyInfo.id, 5, 2);
            this.$loading().close();
          } else {
            this.$loading().close(); // this.$message.warning('Oops!.. Your profile is incomplete. ')

            this.$router.push({
              path: '/profile/contact/user',
              query: {
                i: 5
              }
            });
          }
        }
      }).catch(err => {
        console.log(err);
        this.$loading().close();
        this.$message.error(err.msg);
      });
    },

    changeIdentity(companyId, identity, language) {
      let params = {
        company_id: companyId,
        language: language,
        identity: identity
      };
      SWITCH_IDENTITY_V2(params).then(res => {
        // console.log(res)
        if (res.code == 200) {
          this.$store.commit('allIdentityChanged', true);
          localStorage.setItem('company_id', companyId);
          localStorage.setItem('identity', identity);
          let str = JSON.stringify(res.message);
          localStorage.setItem('menuData', str);
          this.$store.commit('identity', identity);
          this.$store.commit('menuData', res.message);
          this.$loading().close();
        }
      }).catch(err => {
        console.log(err);
        this.$loading().close();
        this.$message.error(err.msg);
      });
    }

  }
};